@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";

.project {
    width: 450px;
    background-color: $bgSecondary;
    border: 1px dashed;
    border-radius: 3px;
    .backgroundIcon {
        width: 100%;
        height: 200px;
        @include flexCenter();
        @include imgCenter();
        //background-size: cover;
        //background-position: center;
        //background-repeat: no-repeat;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            background-color: $bgSecondary;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            opacity: 0.5;
        }
        &:hover {
            &::before{
                opacity: .2;
            }
        }
        a {
            width: 65px;
            height: 45px;
            border-radius: 3px;
            background: rgba(51, 51, 51, 0.5);
            border: none;
            font-family: 'Montserrat Subrayada', sans-serif;
            color: $textColor;
            @include flexCenter();
            position: relative;
            z-index: 1;
            &:hover {
                text-decoration: none;
                background: rgba(51, 51, 51, 0.1);
                color: rgb(51, 51, 51);
                font-weight: 700;
            }
        }
    }
    .projectInfo {
        padding: 25px 20px;
        .projectTitle {
            @include text ($TitleFontFamily, 1.2, 16px, $textColor);
            //font-family: 'Montserrat', sans-serif;
            //color: $textColor;
            //letter-spacing: 1px;
            //font-size: 16px;
            //line-height: 1.2;
            margin: 0 0 15px;
        }
        span {
            @include text($TextFontFamily, 1.8, 16px, $textColor);
            //font-family: 'Poppins', sans-serif;
            //color: $textColor;
            //letter-spacing: 1px;
            //font-size: 16px;
            //line-height: 1.8;
            font-weight: 300;
        }
    }
}

